import React from 'react';
import ReactPhoneInput from 'react-phone-input-2';
import "react-phone-input-2/lib/style.css";
import { Row, Col } from 'react-bootstrap';
import * as AppLabels from "../../helper/AppLabels";
import * as Constants from "../../helper/Constants";

export default class CustomPhoneInput extends React.Component {
    constructor(props, context) {
        super(props, context);
        this.state = {
            inputFocus: false,
            value: '',
            data: ''
        };
    }

    onFocusChange = (focus) => {
        this.setState({
            inputFocus: focus
        })
    }

    render() {
        let phoneValue = this.props.phone ? this.props.phone : ('+' + Constants.DEFAULT_COUNTRY_CODE)
        return (
            <div className={"phone-input-section" + (this.props.isFormLeft ? ' phone-input-from-left' : '') + (this.state.inputFocus ? ' phone-input-focus' : '')}>
                {!this.props.isLabelHide &&
                    <Row>
                        <Col xs={12}>
                            <div className="phone-no-label text-center">
                                {AppLabels.ENTER_MOBILE_NUMBER}
                            </div>
                        </Col>
                    </Row>
                }
                {this.props.isFormLeft &&
                    <Row>
                        <Col xs={12}>
                            <div className="phone-no-label text-center">
                                {AppLabels.MOBILE_NUMBER}
                            </div>
                        </Col>
                    </Row>
                }
                <ReactPhoneInput
                    inputExtraProps={{
                        name: 'phone',
                        required: true,
                        id: this.props.id || ''
                    }}
                    countryCodeEditable={false}
                    autoFormat={false}
                    enableSearchField={true}
                    preferredCountries={[Constants.DEFAULT_COUNTRY]}
                    value={phoneValue}
                    onChange={this.props.handleOnChange}
                    onFocus={() => this.onFocusChange(true)}
                    onBlur={() => this.onFocusChange(false)}
                    disableDropdown={Constants.ONLY_SINGLE_COUNTRY == 1}

                />
            </div>
        );
    }
}
