import React from "react";
import { MyContext } from '../../InitialSetup/MyProvider';
import { Utilities } from '../../Utilities/Utilities';
import CountdownTimer from '../../views/CountDownTimer';
import { MomentDateComponent } from "./CustomComponents";
import * as AppLabels from "../../helper/AppLabels";
import * as Constants from "../../helper/Constants";

export default class MatchInfo extends React.Component {

    render() {
        const { item, status, timerCallback, onlyTimeShow } = this.props;
        let lengthFixture = item.match_list ? item.match_list.length : 0
        let match_item = lengthFixture >= 1 ? item.match_list[0] : item

        return (
            <MyContext.Consumer>
                {(context) => (
                    <div className={"match-info-section" + (onlyTimeShow ? ' match-time-only' : '')}>
                        {
                            ((Constants.SELECTED_GAMET == Constants.GameType.Pred || lengthFixture <= 1) && !onlyTimeShow) && Constants.SELECTED_GAMET != Constants.GameType.MultiGame &&
                            <div className="section-left">
                                <img src={Utilities.teamFlagURL(match_item.home_flag)} alt="" className="home-team-flag" />
                            </div>
                        }
                        <div className="section-middle">
                            {
                                ((Constants.SELECTED_GAMET == Constants.GameType.Pred) || Constants.SELECTED_GAMET != Constants.GameType.MultiGame)
                                    ?
                                    !onlyTimeShow &&
                                    <div>
                                        <span className="team-home">{match_item.home}</span>
                                        <span className="vs-text">{AppLabels.VERSES}</span>
                                        <span className="team-away">{match_item.away}</span>
                                    </div>
                                    :

                                    <span className="team-home">
                                        {item.collection_name}
                                    </span>
                            }
                            {
                                status !== Constants.CONTEST_LIVE && <div className="match-timing">
                                    {

                                        Utilities.showCountDown(item) && status !== Constants.CONTEST_COMPLETED ?
                                            <div className="countdown time-line">
                                                {item.game_starts_in && (Utilities.minuteDiffValue({ date: item.game_starts_in }) <= 0) && <CountdownTimer timerCallback={timerCallback} deadlineTimeStamp={item.game_starts_in} />}
                                            </div> :
                                            <span> <MomentDateComponent data={{ date: item.season_scheduled_date, format: "D MMM - hh:mm A " }} /></span>
                                    }
                                </div>
                            }
                        </div>
                        {
                            ((Constants.SELECTED_GAMET == Constants.GameType.Pred || lengthFixture <= 1) && !onlyTimeShow) && Constants.SELECTED_GAMET != Constants.GameType.MultiGame &&
                            <div className="section-right">
                                <img src={Utilities.teamFlagURL(match_item.away_flag)} alt="" className="away-team-flag" />
                            </div>   }

                    </div>
                )}
            </MyContext.Consumer>
        )
    }

}