import React from 'react';
import { OverlayTrigger, Tooltip, Alert } from 'react-bootstrap';
import CollectionSlider from "../../views/CollectionSlider";
import * as AppLabels from "../../helper/AppLabels";
import * as WSC from "../../WSHelper/WSConstants";
import { Utilities, _Map, _isEmpty } from '../../Utilities/Utilities';
import { SportsIDs } from "../../JsonFiles";
import { AppSelectedSport, MATCH_TYPE } from '../../helper/Constants';
import * as Constants from "../../helper/Constants";
import { getMyContest, getMiniLeagueMyContest } from '../../WSHelper/WSCallings';
import Skeleton from 'react-loading-skeleton';
import { MomentDateComponent } from '../CustomComponent';
import Images from '../../components/images';

export default class CompletedContest extends React.Component {

    constructor(props) {
        super(props)
        this.state = {
            sports_id: Constants.AppSelectedSport,
            completedContestList: [],
            loadingIndex: -1,
        };
    };

    /**
     * @description This function is responsible to get Live Contests response
     * @param status selected tab (Live, Upcoming, Completed)
     */
    getMyContestList(item, idx) {
        if (item.isExpanded) {
            let completedContestList = this.state.completedContestList;
            item['isExpanded'] = false;
            completedContestList[idx] = item;
            this.setState({ completedContestList })
        } else {
            if (item.contest && item.contest.length > 0) {
                let completedContestList = this.state.completedContestList;
                item['isExpanded'] = true;
                completedContestList[idx] = item;
                this.setState({ completedContestList })
            } else {
                var param = {
                    "sports_id": AppSelectedSport,
                    "status": 2,
                    "collection_master_id": item.collection_master_id
                }
                this.setState({ loadingIndex: idx })
                let apiStatus = Constants.SELECTED_GAMET == Constants.GameType.Free2Play ? getMiniLeagueMyContest : getMyContest

                apiStatus(param).then((responseJson) => {
                    this.setState({ loadingIndex: -1 })

                    if (responseJson && responseJson.response_code == WSC.successCode) {
                        let completedContestList = this.state.completedContestList;
                        item['contest'] = responseJson.data;
                        item['isExpanded'] = true;
                        completedContestList[idx] = item;
                        this.setState({ completedContestList })
                    }
                })
            }
        }
    }

    UNSAFE_componentWillReceiveProps(nextProps) {
        if (this.state.completedContestList !== nextProps.completedContestList) {
            this.setState({ completedContestList: nextProps.completedContestList })
        }
    }
    getPrizeAmount = (prize_data) => {
        let prize_text = "Prizes";
        let is_tie_breaker = 0;
        let prizeAmount = { 'real': 0, 'bonus': 0, 'point': 0 };
        return (
            <React.Fragment>
                {
                    prize_data && prize_data.map(function (lObj, lKey) {
                        var amount = 0;
                        if (lObj.max_value) {
                            amount = parseFloat(lObj.max_value);
                        } else {
                            amount = parseFloat(lObj.amount);
                        }
                        if (lObj.prize_type == 3) {
                            is_tie_breaker = 1;
                        }
                        if (lObj.prize_type == 0) {
                            prizeAmount['bonus'] = parseFloat(prizeAmount['bonus']) + amount;
                        } else if (lObj.prize_type == 2) {
                            prizeAmount['point'] = parseFloat(prizeAmount['point']) + amount;
                        } else {
                            prizeAmount['real'] = parseFloat(prizeAmount['real']) + amount;
                        }
                    })
                }
                {
                    is_tie_breaker == 0 && prizeAmount.real > 0 ?
                        <span className="contest-prizes">{Utilities.getMasterData().currency_code}{parseFloat(prizeAmount.real).toFixed(0)}</span>
                        : is_tie_breaker == 0 && prizeAmount.bonus > 0 ? <span className="contest-prizes" ><i className="icon-bonus" width="13px" height="14px" />{parseFloat(prizeAmount.bonus).toFixed(0)}</span>
                            : is_tie_breaker == 0 && prizeAmount.point > 0 ? <span style={{ display: 'inlineBlock' }}> <img style={{ marginTop: '3px' }} src={Images.IC_COIN} width="12px" height="12px" />{parseFloat(prizeAmount.point).toFixed(0)}</span>
                                : AppLabels.PRIZES
                }
                
            </React.Fragment>
        )


    }

    render() {
        let { ContestDetailShow, openLeaderboard } = this.props;
        return (
            <div>
                {
                    this.state.completedContestList.length > 0 &&

                    _Map(this.state.completedContestList, (item, idx) => {

                        return (
                            <div key={idx} className={"contest-card completed-contest-card-new" + (Constants.SELECTED_GAMET == Constants.GameType.MultiGame && item.match_list && item.match_list.length > 1 ? ' contest-card-with-collection' : '')}>
                                {
                                    item.custom_message != '' && item.custom_message != null &&
                                    <div className="bhopu-mycontest">
                                        <OverlayTrigger trigger={['click']} placement="left" overlay={
                                            <Tooltip id="tooltip" className="tooltip-featured">
                                                <strong>{"item.custom_message"} </strong>
                                            </Tooltip>
                                        }>
                                            <i className="icon-megaphone" onClick={(e) => e.stopPropagation()}></i>
                                        </OverlayTrigger>
                                    </div>

                                }
                                <div onClick={() => this.getMyContestList(item, idx)} className={"contest-card-header pointer-cursor" + (item.isExpanded ? ' pb12' : '')}>
                                    <ul>
                                        {(!item.match_list || item.match_list.length < 2) &&
                                            <React.Fragment>
                                                <li className="team-left-side">

                                                    <div className="team-content-img">
                                                        {Constants.SELECTED_GAMET != Constants.GameType.MultiGame &&
                                                            <img src={item.home_flag ? Utilities.teamFlagURL(item.home_flag) : ""} alt="" />
                                                        }
                                                        {Constants.SELECTED_GAMET == Constants.GameType.MultiGame &&
                                                            <img src={item.match_list ? Utilities.teamFlagURL(item.match_list[0].home_flag) : ""} alt="" />
                                                        }
                                                    </div>
                                                    <div className="contest-details-action">
                                                        {Constants.SELECTED_GAMET != Constants.GameType.MultiGame &&
                                                            <React.Fragment>
                                                                <div className="contest-details-first-div">{item.home}</div>
                                                                {
                                                                    (item.score_data && item.score_data[1] && AppSelectedSport == SportsIDs.cricket) ?
                                                                    <div className="contest-details-sec-div">
                                                                    {item.score_data[1].home_team_score}-{(item.score_data[1].home_wickets) ? item.score_data[1].home_wickets : 0}
                                                                    <span className="gray-color-class"> {(item.score_data[1].home_overs) ? item.score_data[1].home_overs : 0} {item.score_data[2] ? ' & ' : ''} </span>
                                                                    {
                                                                        item.score_data[2] && <div className="contest-details-sec-div second-inning">
                                                                        {item.score_data[2].home_team_score}-{(item.score_data[2].home_wickets) ? item.score_data[2].home_wickets : 0}
                                                                        <span className="gray-color-class"> {(item.score_data[2].home_overs) ? item.score_data[2].home_overs : 0} </span>
                                                                        </div>
                                                                    }
                                                                    </div>
                                                                        :
                                                                        (item.score_data) &&
                                                                        <div className="contest-details-sec-div">{item.score_data.home_score}</div>
                                                                }
                                                            </React.Fragment>
                                                        }
                                                        {Constants.SELECTED_GAMET == Constants.GameType.MultiGame && item.match_list.length > 1 &&
                                                            <React.Fragment>
                                                                <div className="contest-details-first-div">{item.match_list[0].home}</div>
                                                                {
                                                                    (item.match_list[0].score_data && item.match_list[0].score_data[1] && AppSelectedSport == SportsIDs.cricket) ?
                                                                        <div className="contest-details-sec-div">
                                                                            {item.match_list[0].score_data[1].home_team_score}-{(item.match_list[0].score_data[1].home_wickets) ? item.match_list[0].score_data[1].home_wickets : 0}
                                                                            <span className="gray-color-class">
                                                                                {(item.match_list[0].score_data[1].home_overs) ? item.match_list[0].score_data[1].home_overs : 0}
                                                                            </span>
                                                                        </div>
                                                                        :
                                                                        (item.match_list[0].score_data) &&
                                                                        <div className="contest-details-sec-div">{item.match_list[0].score_data.home_score}</div>
                                                                }
                                                            </React.Fragment>
                                                        }
                                                    </div>
                                                </li>
                                                <li className="progress-middle">

                                                    <div className="progress-middle-div">
                                                        <div className="team-content">
                                                            <p>
                                                                {item.league_name}
                                                                {/* {
                                                                    AppSelectedSport === '7' &&
                                                                    <React.Fragment>- {MATCH_TYPE[item.format]}</React.Fragment>
                                                                } */}
                                                            </p>
                                                            <span className="time-line primary-color"> <MomentDateComponent data={{ date: item.season_scheduled_date, format: "D MMM - hh:mm A" }} /> </span>

                                                        </div>
                                                    </div>
                                                </li>
                                                <li className="team-right-side">

                                                    <div className="contest-details-action">
                                                        {Constants.SELECTED_GAMET != Constants.GameType.MultiGame &&
                                                            <React.Fragment>
                                                                <div className="contest-details-first-div">{item.away}</div>
                                                                {
                                                                    (item.score_data && item.score_data[1] && AppSelectedSport == SportsIDs.cricket) ?
                                                                    <div className="contest-details-sec-div">
                                                                    {item.score_data[1].away_team_score}-{(item.score_data[1].away_wickets) ? item.score_data[1].away_wickets : 0}
                                                                    <span className="gray-color-class"> {(item.score_data[1].away_overs) ? item.score_data[1].away_overs : 0} {item.score_data[2] ? ' & ' : ''} </span>
                                                                    {
                                                                        item.score_data[2] && <div className="contest-details-sec-div second-inning">
                                                                        {item.score_data[2].away_team_score}-{(item.score_data[2].away_wickets) ? item.score_data[2].away_wickets : 0}
                                                                        <span className="gray-color-class"> {(item.score_data[2].away_overs) ? item.score_data[2].away_overs : 0} </span>
                                                                        </div>
                                                                    }
                                                                    </div>
                                                                        :
                                                                        (item.score_data) &&
                                                                        <div className="contest-details-sec-div">{item.score_data.away_score}</div>
                                                                }
                                                            </React.Fragment>
                                                        }
                                                        {Constants.SELECTED_GAMET == Constants.GameType.MultiGame && item.match_list.length > 1 &&
                                                            <React.Fragment>
                                                                <div className="contest-details-first-div">{item.match_list[0].away}</div>
                                                                {
                                                                    (item.match_list[0].score_data && item.match_list[0].score_data[1] && AppSelectedSport == SportsIDs.cricket) ?
                                                                        <div className="contest-details-sec-div">
                                                                            {item.match_list[0].score_data[1].away_team_score}-{(item.match_list[0].score_data[1].away_wickets) ? item.match_list[0].score_data[1].away_wickets : 0}
                                                                            <span className="gray-color-class">
                                                                                {(item.match_list[0].score_data[1].away_overs) ? item.match_list[0].score_data[1].away_overs : 0}
                                                                            </span>
                                                                        </div>
                                                                        :
                                                                        (item.match_list[0].score_data) &&
                                                                        <div className="contest-details-sec-div">
                                                                            {item.match_list[0].score_data.away_score}
                                                                        </div>
                                                                }
                                                            </React.Fragment>
                                                        }

                                                    </div>
                                                    <div className="team-content-img">
                                                        {Constants.SELECTED_GAMET != Constants.GameType.MultiGame &&
                                                            <img src={item.away_flag ? Utilities.teamFlagURL(item.away_flag) : ""} alt="" />
                                                        }
                                                        {Constants.SELECTED_GAMET == Constants.GameType.MultiGame &&
                                                            <img src={item.match_list ? Utilities.teamFlagURL(item.match_list[0].away_flag) : ""} alt="" />
                                                        }
                                                    </div>
                                                </li>
                                            </React.Fragment>
                                        }
                                        {Constants.SELECTED_GAMET == Constants.GameType.MultiGame && item.match_list.length > 1 &&
                                            <li className="progress-middle">
                                                <div className="team-content">
                                                    <p className="collection_name">{item.collection_name}</p>
                                                    <div className="collection-match-info">
                                                        {item.match_list.length} {AppLabels.MATCHES_SM}
                                                        <span className="circle-divider"></span>
                                                        {item.league_name}
                                                        <span className="circle-divider"></span>
                                                        <MomentDateComponent data={{ date: item.season_scheduled_date, format: "D MMM - hh:mm A" }} />
                                                    </div>
                                                </div>
                                            </li>
                                        }
                                    </ul>
                                </div>
                                {Constants.SELECTED_GAMET == Constants.GameType.MultiGame && item.match_list.length > 1 &&
                                    <div onClick={() => this.getMyContestList(item, idx)} className="mycontest-collection-wrapper">
                                        <CollectionSlider contestSliderData={item} collectionInfo={false} isFrom={'CompletedContest'} />
                                    </div>
                                }

                                {item.isExpanded && item.custom_message != '' && item.custom_message != null &&
                                    <div className="m-b-15 padding-strip">
                                        {
                                            item.custom_message != '' && item.custom_message != null &&
                                            <Alert variant="warning" className="alert-warning msg-alert-container">
                                                <div className="msg-alert-wrapper">
                                                    <span className=""><i className="icon-megaphone"></i></span>
                                                    <span>{item.custom_message}</span>
                                                </div>
                                            </Alert>

                                        }
                                    </div>
                                }

                                {

                                    item.isExpanded && _Map(item.contest, (childItem, idx) => {
                                        return (
                                            <div key={idx} className={"contest-card-body xmb20 ml15 mr15 " + (idx != 0 ? "mt15" : '')}>
                                                <div className="contest-card-body-header cursor-pointer" onClick={() => ContestDetailShow(childItem, item)}>
                                                    <div className="contest-details">
                                                        <div className="contest-details-action">
                                                            {
                                                                childItem.contest_title ?
                                                                <h4>
                                                                    {childItem.contest_title}
                                                                </h4>
                                                                :
                                                                <h4>
                                                                    <span className=" text-capitalize">{AppLabels.WIN} </span>
                                                                    <span>
                                                                        {this.getPrizeAmount(childItem.prize_distibution_detail)}
                                                                    </span>
                                                                </h4>
                                                            }
                                                            {
                                                                childItem.max_bonus_allowed != '0' &&
                                                                <ul className="list-inner">
                                                                    <li className='f-red'>
                                                                        {childItem.max_bonus_allowed}{'% '}{AppLabels.BONUS}
                                                                    </li>
                                                                </ul>
                                                            }
                                                        </div>
                                                    </div>
                                                    <div onClick={(e) => openLeaderboard(e, childItem, item)} className="contest-details-right absolute">
                                                        <a>
                                                            <i className="icon-standings f-sm"></i>
                                                            <span>{AppLabels.STANDINGS}</span>
                                                        </a>
                                                        {
                                                            item.status == 2 && item.status_overview == 4 && childItem.status != 3 && 
                                                            <div className="review-text">{AppLabels.WINNING_REFLECT_SHORTLY}</div>
                                                        }
                                                    </div>
                                                </div>
                                                <div>
                                                    <table className="contest-listing-table">
                                                        <thead>
                                                            <tr>
                                                                <th><span>{AppLabels.Team}</span></th>
                                                                <th>{<span>{AppLabels.WON}</span>}</th>
                                                                <th className="contest-rank-th"><span className="rank-label">{AppLabels.RANK}</span></th>
                                                            </tr>
                                                        </thead>
                                                        <tbody>
                                                            {
                                                                _Map(childItem.teams, (teamItem, idx) => {
                                                                    return (
                                                                        <tr key={teamItem.lineup_master_id}>
                                                                            <td className="team-name">
                                                                                {teamItem.team_name}
                                                                                {
                                                                                        Constants.SELECTED_GAMET == Constants.GameType.DFS && Utilities.getMasterData().a_guru == '1' && teamItem.is_pl_team && teamItem.is_pl_team == '1' &&
                                                                                        <img style={{ marginLeft: 10 }} src={Images.PL_LOGO} alt=''></img>
                                                                                    }
                                                                            </td>
                                                                            {
                                                                                teamItem.is_winner == 1 && teamItem.prize_data != null && !_isEmpty(teamItem.prize_data)
                                                                                    ?
                                                                                    <td className="text-left winning-td" style={{ display: 'flex' }}>
                                                                                        {

                                                                                            _Map(teamItem.prize_data, (prizeItem, idx) => {

                                                                                                return (

                                                                                                    <div>
                                                                                                        {
                                                                                                            (prizeItem.prize_type == 0) ?
                                                                                                                <div className='winning'>
                                                                                                                    <span className="contest-prizes" >
                                                                                                                        {<i style={{ display: 'inlineBlock' }} className="icon-bonus"></i>}
                                                                                                                        {teamItem.prize_data.length === idx + 1 ? prizeItem.amount : prizeItem.amount + "/"}
                                                                                                                    </span>
                                                                                                                </div>
                                                                                                                :
                                                                                                                (prizeItem.prize_type == 1) ?
                                                                                                                    <div className='winning'>

                                                                                                                        {<span className="contest-prizes">{Utilities.getMasterData().currency_code}
                                                                                                                        {teamItem.prize_data.length === idx + 1 ? parseFloat(prizeItem.amount).toFixed(2) : parseFloat(prizeItem.amount).toFixed(2) + "/"}</span> }
                                                                                                                        
                                                                                                                    </div>
                                                                                                                    :
                                                                                                                    (prizeItem.prize_type == 2) ?
                                                                                                                        <div className='winning'>
                                                                                                                            {<span className="contest-prizes" style={{ display: 'flex' }}>
                                                                                                                                <img src={Images.IC_COIN} width="15px" height="15px" style={{position: 'Relative', top: 1}} />
                                                                                                                                {teamItem.prize_data.length === idx + 1 ? prizeItem.amount : prizeItem.amount + "/"}</span>}

                                                                                                                        </div>
                                                                                                                        :
                                                                                                                        (prizeItem.prize_type == 3) ?
                                                                                                                            <div className='winning'>
                                                                                                                                {<span className="contest-prizes" style={{ display: 'inlineBlock' }}>{teamItem.prize_data.length === idx + 1 ? prizeItem.name : prizeItem.name + "/"}</span>}

                                                                                                                            </div> : ''

                                                                                                        }

                                                                                                    </div>
                                                                                                )


                                                                                            })
                                                                                        }

                                                                                    </td>
                                                                                    :
                                                                                    <td className="text-left">
                                                                                        {
                                                                                            teamItem.won_prize <= 0 && <div className='winning text-center'>--</div>
                                                                                        }
                                                                                        {
                                                                                            (childItem.prize_type == 0) && teamItem.won_prize > 0 &&
                                                                                            <div className='winning'>
                                                                                                {childItem.prize_pool != "0" && <i style={{ display: 'inlineBlock', position: 'relative', top: -1, marginRight: 3 }} className="icon-bonus"></i>}
                                                                                                {teamItem.won_prize || '0'}
                                                                                            </div>
                                                                                        }
                                                                                        {
                                                                                            (childItem.prize_type == 1) && teamItem.won_prize > 0 &&
                                                                                            <div className='winning contest-prizes'>
                                                                                                {childItem.prize_pool != "0" && <span style={{ marginLeft: 5, marginRight: 5, }}>{Utilities.getMasterData().currency_code}</span>}
                                                                                                {teamItem.won_prize || '0'}
                                                                                            </div>
                                                                                        }
                                                                                    </td>
                                                                            }


                                                                            <td className={"contest-rank" + (teamItem.is_winner == 1 ? ' success' : '')}>
                                                                                <a>
                                                                                    <span>
                                                                                        {
                                                                                            teamItem.is_winner == 1 &&
                                                                                            <i className="icon-trophy"></i>
                                                                                        }
                                                                                    </span>
                                                                                    {teamItem.game_rank}
                                                                                </a>
                                                                            </td>
                                                                        </tr>
                                                                    )
                                                                })
                                                            }

                                                        </tbody>
                                                    </table>
                                                </div>
                                            </div>
                                        )
                                    })
                                }
                                {

                                    (this.state.loadingIndex === idx) && <div className="contest-list m border shadow-none">
                                        <div className="shimmer-container">
                                            <div className="shimmer-top-view">
                                                <div className="shimmer-line">
                                                    <Skeleton height={9} />
                                                    <Skeleton height={6} />
                                                    <Skeleton height={4} width={100} />
                                                </div>
                                                <div className="shimmer-image">
                                                    <Skeleton width={30} height={30} />
                                                </div>
                                            </div>
                                            <div className="shimmer-bottom-view">
                                                <div className="progress-bar-default w-100">
                                                    <Skeleton height={6} />
                                                    <div className="d-flex justify-content-between">
                                                        <Skeleton height={4} width={60} />
                                                        <Skeleton height={4} width={60} />
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                }
                            </div>
                        )
                    })
                }
            </div>
        )
    }

}