import React, {Suspense, lazy} from 'react';
import { Row, Col, FormGroup,Button } from 'react-bootstrap';
import { MyContext } from '../../InitialSetup/MyProvider';
import { Helmet } from "react-helmet";
import CustomHeader from '../../components/CustomHeader';
import MetaData from "../../helper/MetaData";
import * as AppLabels from "../../helper/AppLabels";
import Images from '../../components/images';
import { Utilities } from '../../Utilities/Utilities';
import * as WSC from "../../WSHelper/WSConstants";
import { SELECTED_GAMET, GameType, PAYMENT_TYPE } from "../../helper/Constants";
import { depositPaytmFund, depositPayUmoneyFund, depositGocashFund, joinContest, depositIPAYFund, depositPayPalFund, depositPayStackFund, depositRazorPayFund, joinContestWithMultiTeam, callBuyCoins, getCashFreeGatewayList, joinContestNetworkfantasy, joinContestWithMultiTeamNF } from '../../WSHelper/WSCallings';
import WSManager from '../../WSHelper/WSManager';
